import React from "react";
import PropTypes from "prop-types";
import { Button, TextField } from "@mui/material";
import AccountCircleOutlinedIcon from '@mui/icons-material/AccountCircleOutlined';
import InputAdornment from '@mui/material/InputAdornment';
import LockIcon from '@mui/icons-material/Lock';


const LoginForm = ({
  username,
  password,
  setUsername,
  setPassword,
  handleLogin,
}) => {
  return (
    <div>

      <form onSubmit={handleLogin}>
        <div>
          <TextField
          sx={{m:1,p:1, borderRadius: "0.5rem", color:"white", width: 300}}
          variant="outlined"
          required
         
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
               <AccountCircleOutlinedIcon />
              </InputAdornment>
            ),
          }}
          placeholder="Username"
            id="username"
            type="text"
            value={username}
            name="Username"
            onChange={({ target }) => setUsername(target.value)}
          />
        </div>
        <div>
          <TextField
         sx={{m:1,p:1, borderRadius: "0.5rem", width: 300}}
         InputProps={{
          startAdornment: (
            <InputAdornment position="start">
             <LockIcon />
            </InputAdornment>
          ),
        }}
           required
           variant="outlined"
           placeholder="Password"
            id="password"
            type="password"
            value={password}
            name="Password"
            onChange={({ target }) => setPassword(target.value)}
          />
        </div>
        <Button id="login-button" type="submit" variant="contained" color="primary" sx= {{width: 300, '&:hover':{backgroundColor: "#A8E640"}, fontWeight: 700}}>
          login
        </Button>
      </form>
    </div>
  );
};

LoginForm.propTypes = {
  username: PropTypes.string.isRequired,
  password: PropTypes.string.isRequired,
  setUsername: PropTypes.func.isRequired,
  setPassword: PropTypes.func.isRequired,
  handleLogin: PropTypes.func.isRequired,
};
export default LoginForm;
