import { Container, Typography  } from "@mui/material"
import { AppBar,Toolbar,Stack, Button} from '@mui/material';
import { Link } from 'react-router-dom'; 
import SignUpForm from "./SignUpForm";


 
const SignUp = () => {
   

    return (
        <div>
           
        <AppBar position='static' sx={{backgroundColor: "#e5e7eb", mb: 5,  width: "100%"}}>
         <Toolbar>

         <img src='/Terrapost.png' alt="Logo" style={{maxWidth: 150, margin: "1rem"}} />
         <Typography  component="div" style={{flex: 1}} > </Typography>
  

           <Stack direction="row" spacing={5} >
           <Button component={Link} variant="outlined" color="primary" underline="none" to='/'   sx={{'&:hover': {backgroundColor: '#A8E640', fontWeight: 900 }   }}>
              <Typography align="right" variant="body1" > Login </Typography>
            </Button>

        <Button component={Link} variant="contained" color="primary" underline="none" to='/signup' >
        <Typography  align="right" variant="body1" sx={{'&:hover': {color: "#A8E640", fontWeight: 900 }  }}> SignUp </Typography>
        </Button>
        </Stack> 
  
         

        </Toolbar>
       </AppBar>

       <Typography align="center" gutterBottom="true" variant="body1" sx={{p: "1rem", m: "1rem", color: "gray", fontSize: "1.5rem", fontWeight: 600}}>
        Create a New Account
        </Typography>

            
        <Container maxWidth='sm' 
        sx={{backgroundColor:'#e5e7eb', width: 450, height: 500, borderRadius: "1.2rem", mb: 8,
        display: "flex", alignItems: "center", justifyContent: "center"}} >
          
          <Typography align="center" gutterBottom="true" variant="body1">
    
        <SignUpForm />
        </Typography>
        </Container>
        </div>
    )
}

export default SignUp 