
import React from "react"
import { useState } from "react"
import { useSelector } from "react-redux"
import { useParams } from "react-router-dom"
import { Button, TextField, Card,CardContent, Typography, Box,Link, Stack } from "@mui/material"
import ThumbUpAltIcon from '@mui/icons-material/ThumbUpAlt';
import DeleteIcon from '@mui/icons-material/Delete';
import CommentIcon from '@mui/icons-material/Comment';
import ForumIcon from '@mui/icons-material/Forum';
import SendIcon from '@mui/icons-material/Send';
import InputAdornment from '@mui/material/InputAdornment';
const randomMC = require('randomcolor');

  const DetailedBlogPost = (props) => {
    const blogs = useSelector(state => state.blogs)
    const user = useSelector(state => state.users)
    const blogid = useParams().blogid
  const detailedBlog = blogid
  ? blogs.filter(blog => blog.id === blogid)
  : null

  const isOriginalUser = user.username === detailedBlog[0].user.username

  const cardHeight = isOriginalUser ? 300 : 230

  const [comment,setComment] = useState("")

    console.log("INSIDE DETAILED BLOG", blogs, detailedBlog)
    return (
      <div>
      <div style={{display:"flex", justifyContent: "center"}}>
      
        <Card elevation={5}  sx={{ height: cardHeight, width: 600, maxWidth: 800}} >
          <CardContent>
          <Typography variant="h5" sx={{m:1, fontWeight: 700, fontSize: "28px"}} >
            {detailedBlog[0].title}
            </Typography>
            <Typography variant="caption" sx={{m:1, fontWeight: 700, fontSize: "18px", color: "gray"}} >
            {detailedBlog[0].author}
            </Typography>
            <Typography variant="body1"  sx={{m:1, fontSize: "18px"}}>
              {detailedBlog[0].content}              
            </Typography>

            <Typography variant="body2" >
              <Link underline="none" sx={{m:1, fontSize: "24px", '&:hover': {textDecoration: "underline"}}} style={{cursor: "pointer"}}>{detailedBlog[0].url}   </Link>            
            </Typography>

            <Typography variant="body1"  sx={{m:1, fontSize: "14px", fontWeight: 400, color: "orange"}}>
            Added by <span style={{backgroundColor: "orange", color:"red", padding: 5}} > {detailedBlog[0].user.name}  </span>          
           
           <span style= {{margin :"20px", marginRight: "10px"}} >
            {detailedBlog[0].likes} likes
            </span>
            <span style= {{margin :"5px"}} > 
            <Button 
            style={{maxWidth: '20px', maxHeight: '20px', minWidth: '20px', minHeight: '20px'}}
            onClick={() => props.likeBlog(detailedBlog[0].id)}
            variant="outlined"
            startIcon={<ThumbUpAltIcon />}  /> 
            </span>
            </Typography>


        <div>
        {user.username === detailedBlog[0].user.username && (
          <Button onClick={() => props.deleteBlog(detailedBlog[0].id)}
                  sx= {{m: 1, mt: 5}}
                   startIcon={<DeleteIcon />}
                   variant="outlined"
                   color="error" >
                     <Typography> Delete </Typography>
            </Button> )}
        </div>
            
    </CardContent>

    </Card>
    </div>

    <div>      
      <Typography variant="h4" align="center" component="div" sx={{m: "1rem", mt: "3rem", fontSize: "18px", fontWeight: 700, color: "gray"}}> <CommentIcon /> Comments  </Typography>

        
<div  style={{display:"flex", justifyContent: "center", alignItems: "center", flexDirection: "row" }}>

        <TextField 
        size= 'xs'
        sx = {{m: "1rem", ml: "10rem", mb:"0.5rem", width: 600}}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
             <ForumIcon />
            </InputAdornment>
          ),
        }}
        color="secondary" 
        type="text" 
        placeholder="comment"
        value={comment} 
        onChange={({ target }) => setComment(target.value)} />
        <span>
        <Button onClick={() => {
          setComment('')
          return props.commentBlog(detailedBlog[0].id, comment)}} variant="outlined" color="primary"> <SendIcon /> </Button>
        </span>
        
      </div>
      
      <Box sx={{display:"flex", justifyContent: "center", alignItems: "center", flexDirection:"column"}}>
      {detailedBlog[0].comments.map(comment => {
        var color = randomMC( {luminosity: 'light'});
return (
  <ul >
    <Typography variant="body1" component="div" sx = {{m:"0.01rem", ml:"4rem", p: "0.5rem", pl:"1.5rem", backgroundColor: color, width: 560, alignItems: "center", color:"gray", fontStyle: "italic", borderRadius: "0.5rem"}}>
        {comment}
        
      </Typography>
  </ul>
     
      )}
  )}

</Box>



     
  </div>

    </div>
    )
  }

  export default DetailedBlogPost